
section {
  background: #efefef;
  border: 1px solid #ccc;
  flex: 1;
  margin: 1rem;
  padding: 1rem;

  div {
    margin: 0.3rem;
  }
}

.loading-true {
  margin: 0 auto;
  border: 16px solid #f3f3f3; 
  border-top: 16px solid #3498db; 
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

